export default "<template>\n  <b-container fluid>\n    <raw-loader :mytxt=\"require('!raw-loader!./BasicBs.vue').default\"></raw-loader>\n    <b-table :items=\"items\" :fields=\"fields\" :current-page=\"currentPage\" :per-page=\"perPage\" :filter=\"filter\"\n      :filter-included-fields=\"filterOn\" :sort-by.sync=\"sortBy\" :sort-desc.sync=\"sortDesc\"\n      :sort-direction=\"sortDirection\" stacked=\"md\" show-empty small fixed outlined bordered @filtered=\"onFiltered\">\n\n      <template #cell(cmd)=\"row\">\n        <b-button variant=\"outline\" class=\"mr-1\" size=\"sm\" @click=\"OnEditModal(row.item)\">\n          <b-icon-pencil-square></b-icon-pencil-square>\n        </b-button>\n        <b-button variant=\"outline\" class=\"mr-1\" size=\"sm\" @click=\"OnDel(row.item)\">\n          <b-icon-trash></b-icon-trash>\n        </b-button>\n      </template>\n\n      <template #bottom-row=\"data\">\n        <td :colspan=\"data.columns\">\n          <b-row>\n            <b-col sm=\"6\" class=\"my-1\">\n              <b-form-group label=\"Filter\" label-for=\"filter-input\" label-cols-sm=\"3\" label-align-sm=\"right\"\n                label-size=\"sm\" class=\"mb-0\">\n                <b-input-group size=\"sm\">\n                  <b-form-input id=\"filter-input\" v-model=\"filter\" type=\"search\"\n                    placeholder=\"Type to Search\"></b-form-input>\n\n                  <b-input-group-append>\n                    <b-button :disabled=\"!filter\" @click=\"filter = ''\">Clear</b-button>\n                  </b-input-group-append>\n                </b-input-group>\n              </b-form-group>\n            </b-col>\n            <b-col sm=\"6\" class=\"my-1\">\n              <b-form-group label=\"Per page\" label-for=\"per-page-select\" label-cols-sm=\"6\" label-cols-md=\"4\"\n                label-cols-lg=\"3\" label-align-sm=\"right\" label-size=\"sm\" class=\"mb-0\">\n                <b-form-select id=\"per-page-select\" v-model=\"perPage\" :options=\"pageOptions\" size=\"sm\"></b-form-select>\n              </b-form-group>\n\n            </b-col>\n            <b-col sm=\"6\" class=\"my-1 text-right\">\n              page :{{ currentPage }} / {{ getLastPage(totalRows) }} of {{ totalRows }}\n              <b-button @click=\"OnAddModal\" size=\"sm\"><b>Add</b></b-button>\n            </b-col>\n            <b-col sm=\"6\" class=\"my-1\">\n              <b-pagination v-model=\"currentPage\" :total-rows=\"totalRows\" :per-page=\"perPage\" align=\"fill\" size=\"sm\"\n                class=\"my-0\"></b-pagination>\n            </b-col>\n          </b-row>\n\n        </td>\n      </template>\n\n\n\n\n    </b-table>\n\n    <!-- Info modal -->\n    <b-modal ref=\"my-modal\" size=\"sm\" :no-close-on-backdrop=\"true\" hide-footer\n      :title=\"(sel[pri] == 0) ? 'form Add' : 'form Edit'\">\n      <div class=\"d-block\">\n        <form @submit.stop.prevent=\"onSaveModal\">\n          <div class=\"text-right\">\n            <div class=\"form-group row\" v-for=\"(v, k) in sel\" :key=\"k\">\n\n              <template v-if=\"(k == pri)\">\n                <label class=\"col-sm-4 col-form-label\">Id</label>\n                <div class=\"col-sm-8\">\n                  <input :readonly=\"true\" :name=\"k\" v-model=\"sel[k]\" :class=\"{\n                    'form-control': true,\n                    'ko-hop-le': errors.has(k),\n                  }\" type=\"text\" />\n                </div>\n              </template>\n              <template v-if=\"k == 'name'\">\n                <label class=\"col-sm-4 col-form-label\">Name</label>\n                <div class=\"col-sm-8\">\n                  <input :name=\"k\" v-model=\"sel[k]\" autocomplete=\"off\" :class=\"{\n                    'form-control': true,\n                    'ko-hop-le': errors.has(k),\n                  }\" type=\"text\" v-validate=\"'required'\" />\n                </div>\n              </template>\n\n            </div>\n            <button type=\"submit\" class=\"btn mr-1\">Save</button>\n            <button type=\"button\" class=\"btn\" @click=\"hideModal()\">Cancel</button>\n\n          </div>\n        </form>\n      </div>\n    </b-modal>\n  </b-container>\n</template>\n\n<script>\n\nimport VeeValidate from \"vee-validate\";\nimport Vue from \"vue\";\nVue.use(VeeValidate, {\n  classes: true,\n  fieldsBagName: 'veeFields'\n});\n\nimport moment from \"moment\";\n\nimport vSelect from \"vue-select\";\nVue.component(\"v-select\", vSelect);\n//import \"vue-select/dist/vue-select.css\";\n\n\n\nexport default {\n  data() {\n    return {\n      items: [\n      ],\n\n      fields: [\n        {\n          key: 'id',\n          formatter: (value, key, item) => {\n            let text = value.toString();\n            return (text.length < 4) ? text : text.substring(0, 4) + \"...\";\n          },\n          sortable: true,\n          thClass: 'text-center',\n          tdClass: 'text-center align-middle',\n          //tdClass: 'align-middle'\n          thStyle: { width: \"10%\" },\n        },\n        { key: 'name', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"80%\" } },\n        {\n          key: 'cmd',\n          class: 'text-center',\n          thClass: 'text-center',\n          tdClass: 'align-middle',\n          thStyle: { width: \"10%\" }\n        }\n      ],\n\n\n      totalRows: 1,\n      currentPage: 1,\n      perPage: 3,\n      pageOptions: [5, 10, 15, { value: 100, text: \"Show a lot\" }],\n      sortBy: '',\n      sortDesc: false,\n      sortDirection: 'asc',\n      filter: null,\n      filterOn: [],\n      infoModal: {\n        id: 'info-modal',\n        title: '',\n        content: ''\n      },\n\n      sel: {},\n      pri: 'id',\n      title: \"Hi modal\"\n\n\n    }\n  },\n  computed: {\n    sortOptions() {\n      // Create an options list from our fields\n      return this.fields\n        .filter(f => f.sortable)\n        .map(f => {\n          return { text: f.label, value: f.key }\n        })\n    }\n  },\n  mounted() {\n    let items = [\n    ];\n    for (var i = 1; i < 5; i++) {\n      items.push({\n        id: i,\n        name: `thuc ${i}`\n      })\n    }\n    this.items = items;\n    this.totalRows = this.items.length;\n  },\n  methods: {\n    createId(){\n      let max=0;\n      this.items.forEach(x=>{\n        if(x[this.pri] > max){\n          max=x[this.pri];\n        }\n      })\n      return max+1;\n    },\n    OnDel(item) {\n      this.delRow(item);\n    },\n    delRow(item) {\n      let id = item[this.pri];\n      let index = this.items.findIndex(x => x[this.pri] == id);\n      this.items.splice(index, 1);\n      this.totalRows = this.items.length;\n      this.lastPage = this.getLastPage(this.totalRows);\n    },\n    OnEditModal(item) {\n      this.sel = JSON.parse(JSON.stringify(item));\n      this.showModal();\n    },\n    onSaveModal() {\n      this.$validator.validateAll().then((result) => {\n        if (!result) {\n          this.toast(\"Không hợp lệ\");\n          return;\n        }\n        let r = JSON.parse(JSON.stringify(this.sel));\n        if (r[this.pri] == 0) {\n          r[this.pri] = this.createId();//new Date().getTime();\n          this.items.push(r);\n          this.totalRows=this.items.length;\n        } else {\n          let index = this.items.findIndex(x => x[this.pri] == r[this.pri]);\n          this.$set(this.items, index, r);\n        }\n        this.hideModal();\n      });\n    },\n    OnAddModal(){\n      this.sel={\n        id: 0,\n        name: 'Thức',\n      }\n      this.showModal();\n    },    \n    getLastPage(count) {\n      let pageSize = this.perPage;//this.tabulator.getPageSize();\n      let du = count % pageSize;\n      return du == 0 ? count / pageSize : (count - du) / pageSize + 1;\n    },\n    info(item, index, button) {\n      this.infoModal.title = `Row index: ${index}`\n      this.infoModal.content = JSON.stringify(item, null, 2)\n      this.$root.$emit('bv::show::modal', this.infoModal.id, button)\n    },\n    resetInfoModal() {\n      this.infoModal.title = ''\n      this.infoModal.content = ''\n    },\n    onFiltered(filteredItems) {\n      // Trigger pagination to update the number of buttons/pages due to filtering\n      this.totalRows = filteredItems.length\n      this.currentPage = 1\n    },\n    showModal() {\n      this.$refs[\"my-modal\"].show();\n    },\n    hideModal() {\n      this.$refs[\"my-modal\"].hide();\n    },\n\n    toast(msg, variant = null) {\n      this.$bvToast.toast(msg, {\n        title: `Information`,\n        variant: variant,\n        solid: true,\n      });\n    },\n\n\n  }\n}\n</script>";